\n  .button-disabled();\n}\n.button-color(@color; @background; @border) {\n  color: @color;\n  background: @background;\n  border-color: @border; // a inside Button which only work in Chrome\n  // http://stackoverflow.com/a/17253457\n  > a:only-child {\n    color: currentColor;\n    &::after {\n      position: absolute;\n      top: 0;\n      right: 0;\n      bottom: 0;\n      left: 0;\n      background: transparent;\n      content: '';\n    }\n  }\n}\n.button-group-base(@btnClassName) {\n  position: relative;\n  display: inline-flex;\n  > .@{btnClassName},\n  > span > .@{btnClassName} {\n    position: relative;\n    &:hover,\n    &:focus,\n    &:active {\n      z-index: 2;\n    }\n    &[disabled] {\n      z-index: 0;\n    }\n  }\n  .@{btnClassName}-icon-only {\n    font-size: @font-size-base;\n  }\n  // size\n  &-lg > .@{btnClassName},\n  &-lg > span > .@{btnClassName} {\n    .button-size(@btn-height-lg; @btn-padding-horizontal-lg; @btn-font-size-lg; 0);\n  }\n  &-lg .@{btnClassName}.@{btnClassName}-icon-only {\n    .square(@btn-height-lg);\n    padding-right: 0;\n    padding-left: 0;\n  }\n  &-sm > .@{btnClassName},\n  &-sm > span > .@{btnClassName} {\n    .button-size(@btn-height-sm; @btn-padding-horizontal-sm; @font-size-base; 0);\n    > .@{iconfont-css-prefix} {\n      font-size: @font-size-base;\n    }\n  }\n  &-sm .@{btnClassName}.@{btnClassName}-icon-only {\n    .square(@btn-height-sm);\n    padding-right: 0;\n    padding-left: 0;\n  }\n}\n// Base styles of buttons\n// --------------------------------------------------\n.btn() {\n  position: relative;\n  display: inline-block;\n  font-weight: @btn-font-weight;\n  white-space: nowrap;\n  text-align: center;\n  background-image: none;\n  border: @btn-border-width @btn-border-style transparent;\n  box-shadow: @btn-shadow;\n  cursor: pointer;\n  transition: all 0.3s @ease-in-out;\n  user-select: none;\n  touch-action: manipulation;\n  .button-size(\n    @btn-height-base; @btn-padding-horizontal-base; @font-size-base; @btn-border-radius-base\n  );\n  > .@{iconfont-css-prefix} {\n    line-height: 1;\n  }\n  &,\n  &:active,\n  &:focus {\n    outline: 0;\n  }\n  &:not([disabled]):hover {\n    text-decoration: none;\n  }\n  &:not([disabled]):active {\n    outline: 0;\n    box-shadow: none;\n  }\n  &[disabled] {\n    cursor: not-allowed;\n    > * {\n      pointer-events: none;\n    }\n  }\n  &-lg {\n    .button-size(\n      @btn-height-lg; @btn-padding-horizontal-lg; @btn-font-size-lg; @btn-border-radius-base\n    );\n  }\n  &-sm {\n    .button-size(\n      @btn-height-sm; @btn-padding-horizontal-sm; @btn-font-size-sm; @btn-border-radius-sm\n    );\n  }\n}\n// primary button style\n.btn-primary() {\n  .button-variant-primary(@btn-primary-color; @btn-primary-bg);\n}\n// default button style\n.btn-default() {\n  .button-variant-other(@btn-default-color; @btn-default-bg; @btn-default-border);\n  &:hover,\n  &:focus,\n  &:active {\n    text-decoration: none;\n    background: @btn-default-bg;\n  }\n}\n// ghost button style\n.btn-ghost() {\n  .button-variant-other(@btn-ghost-color, @btn-ghost-bg, @btn-ghost-border);\n}\n// dashed button style\n.btn-dashed() {\n  .button-variant-other(@btn-default-color, @btn-default-bg, @btn-default-border);\n  border-style: dashed;\n}\n// danger button style\n.btn-danger() {\n  .button-variant-primary(@btn-danger-color, @btn-danger-bg);\n}\n// danger default button style\n.btn-danger-default() {\n  .button-color(@error-color, @btn-default-bg, @error-color);\n  &:hover,\n  &:focus {\n    & when (@theme = dark) {\n      .button-color(\n        ~`colorPalette('@{error-color}', 7) `; @btn-default-bg; ~`colorPalette('@{error-color}', 7)\n          `\n      );\n    }\n    & when not (@theme = dark) {\n      .button-color(\n        ~`colorPalette('@{error-color}', 5) `; @btn-default-bg; ~`colorPalette('@{error-color}', 5)\n          `\n      );\n    }\n  }\n  &:active {\n    & when (@theme = dark) {\n      .button-color(\n        ~`colorPalette('@{error-colo